body{
  font-family: Poppins;
  background-color: #d9d9d9;
}

.custom-switch{
  padding: 0;
}

.custom-control{
  padding: 0;
}

.header-style {
  z-index: 995;
  /* background: #615d5a; */
  background-color: #ec5d3d;
  height: 73px;
  width: 100%;
  padding: 0;
  -webkit-transition: all 0.6s ease;
  position: fixed;
  display: block;
  flex-wrap: wrap;
  align-items: center;
  box-shadow:0px 3px 5px 0px rgba(0, 0, 0, 0.3);
}

.brand-style {
  -webkit-transition: all 0.6s ease;
  line-height: 2em;
}

.contact-link {
  display: flex;
  margin: 0;
  padding: 0;
  color:white;
}

.d_n{
  margin-top: -30px;
}

.t_10{
  top: 10px;
}

.h_42{
  height: 42px!important;
}



.list-link {
  display: flex;
  margin: 0;
  padding: 0;
  font-size: 16px;
}

.list-link-item {
  list-style: none;
  margin-left: 2em;
  line-height: 4em;
}

a:link {
  text-decoration: none;
  font-weight: 300;
}

a:-webkit-any-link { 
  color:white; 
}

.header-style a:hover {
  color: black;
}

.banner-page {
  padding-top: 100px;
}

.button-style{
    background-color: #ec5d3d;
    color: #fff;
    border: 2px #32446b solid;
    border-radius: 5px;
    margin-top: 2em;
    padding: 15px;
}

.button-style-2:active{
  border: 1px solid black;
}



.m_t_40 {
  margin-top: 40px;
}

.footer-style {
  /* background-color:#1a2228; */
  background-color:#615d5a;
  width: 100%;
  display: block;
  bottom: 0;
}

.footer-style-2 {
  background-color:black;
  width: 100%;
  display: block;
  bottom: 0;
  height: 60px;
}

.content-section {
  padding-top : 140px
}

.contact-page-header {
  font-size: 40px;
  text-align-last: center;
  margin-bottom: 40px;
}

.form-style {
  text-align: center;
}

.input-style {
  width: 80%;
  height: 50px;
  margin: 20px;
}

.input-style-textarea{
  height: 100px!important;
}

.contact-button-div {
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  text-align: right;
  margin-bottom: 40px;
}

.contact-button-style {
  width: 200px;
}

.contact-container {
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  /* height: 100vh; */
  display: block;
  width:100%;
  margin-bottom: 100px;
}

.info-header {
  letter-spacing: -0.3px;
  color: #131517;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 20px;
}

.info-text {
  color: #7C7C7C;
  font-size: 15px;
}

.footer-text {
  color: white;
}

.footer-header-text {
  color: white;
  font-weight: 400;
}

.d-block{
  display: block;
}

.w-100{
  width:100%;
  height: 350px;
}
